.user-master-layout {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 0px;

  height: 100vh;
  background: #FFFFFF;
}

/* Content area */
.user-content-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding : 0px 0px 60px 0px;
  width: 100%;

  background: var(--color-primary);

  /* Inside auto layout */
  flex: 1;
  order: 0;
  flex-grow: 1;
}
